import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import { FormControl, FormGroup, AbstractControl, FormBuilder, Validators } from '@angular/forms';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-add-coin',
  templateUrl: 'add-coin.html',
})
export class AddCoinPage {

  id: any;
  unitCostBTC: any;
  unitCostINR: any;
  units: any;
  name: any;
  symbol: any;
  add: any;
  title: any;
  coinslist = {};
  list: any;

  addCoinForm: FormGroup;
  unitsN: String = '';
  unitCostBTCN: String = '';
  unitCostINRN: String = '';

  constructor(public navCtrl: NavController, public navParams: NavParams,private storage: Storage,
    public loadingCtrl: LoadingController,public formBuilder: FormBuilder) {
    this.id =  this.navParams.get('id');
    this.name =  this.navParams.get('name');
    this.symbol =  this.navParams.get('symbol');
    this.add = this.navParams.get("add");
    this.unitCostBTC = this.navParams.get("priceBTC");
    this.unitCostINR = this.navParams.get("priceINR");
    //console.log(typeof(this.add), " ", this.add);

    this.addCoinForm = new FormGroup({
     unitsN: new FormControl({ value: '' }, Validators.compose([Validators.required, Validators.pattern('[0-9.0-9]*')])),
     unitCostBTCN: new FormControl({ value: '' }, Validators.compose([Validators.required, Validators.pattern('[0-9.0-9]*')])),
     unitCostINRN: new FormControl({ value: '' }, Validators.compose([Validators.required, Validators.pattern('[0-9.0-9]*')]))
    });
  }



  ionViewDidLoad() {
    //console.log('ionViewDidLoad AddCoinPage');
    if(this.add)
      this.title = "Add";
    else{
      this.title = "Edit";
      let loading = this.loadingCtrl.create({
        content: 'Please wait...'
      });
      loading.present();
      let i;
      this.storage.get("savedCoins").then( (res)=>{
        if(res){
          //console.log("In if");
          this.list = JSON.parse(res);
          //console.log("this.list json: ",this.list);
          for(i=0;i<this.list.length;i++){
              if(this.list[i].id == this.id)
                break;
          }
          if(i<this.list.length){
            this.id = this.list[i].id;
            this.name =this.list[i].name;
            this.symbol =this.list[i].symbol;
            this.unitCostBTC = this.list[i].unitCostBTC;
            this.unitCostINR = this.list[i].unitCostINR;
            this.units = this.list[i].units;
          }
        }
        loading.dismiss();
      });
    }
    //console.log(typeof(this.add), " ", this.add);
  }
  clear(){
    this.storage.clear();
  }
  saveCoin(){
    let i;
    this.storage.get("savedCoins").then( (res)=>{
      if(res){
        this.list = JSON.parse(res);
        console.log("this.list json: ",this.list);
        for(i=0;i<this.list.length;i++){
            if(this.list[i].id == this.id)
              break;
        }
        //existing coin
        if(i<this.list.length){
          this.list[i].id= this.id;
          this.list[i].name= this.name;
          this.list[i].symbol= this.symbol;
          this.list[i].unitCostBTC= this.unitCostBTC;
          this.list[i].unitCostINR= this.unitCostINR;
          this.list[i].units= this.units;
          this.list[i].expenseBTC = (this.unitCostBTC)*(this.units);
          this.list[i].expenseINR = (this.unitCostINR)*(this.units);
          this.list[i].currCostBTC = 0;
          this.list[i].currCostINR = 0;
          this.list[i].roiBTC = 0;
          this.list[i].roiINR = 0;
          this.list[i].market= null;
          console.log(this.list[i]);
        }
        //new coin
        else{
          console.log(this.id);
          this.list.push({
            id: this.id,
            name: this.name,
            symbol: this.symbol,
            unitCostBTC: this.unitCostBTC,
            unitCostINR: this.unitCostINR,
            units: this.units,
            expenseINR: (this.unitCostINR)*(this.units),
            expenseBTC: (this.unitCostBTC)*(this.units),
            currCostBTC: 0,
            currCostINR: 0,
            roiBTC: 0,
            roiINR: 0,
            market: null
          });
        }
        //save to storage
        this.storage.set("savedCoins", JSON.stringify(this.list));
      }
      //1st time saving
      else
        this.storage.set("savedCoins", JSON.stringify([{
          id: this.id,
          name: this.name,
          symbol: this.symbol,
          unitCostBTC: this.unitCostBTC,
          unitCostINR: this.unitCostINR,
          units: this.units,
          expenseINR: (this.unitCostINR)*(this.units),
          expenseBTC: (this.unitCostBTC)*(this.units),
          currCostBTC: 0,
          currCostINR: 0,
          roiBTC: 0,
          roiINR: 0,
          market: null
        }]));
      this.navCtrl.pop();
    });
  }

}



