import { Component } from '@angular/core';
import { IonicPage, NavController, LoadingController, ToastController, AlertController } from 'ionic-angular';
import { Http } from '@angular/http';
import { Storage } from '@ionic/storage';
import { CoinsListingPage } from '../coins-listing/coins-listing';
import { AddCoinPage } from '../add-coin/add-coin';
import { CoinsServiceProvider } from '../../providers/coins-service/coins-service';
import { DevPage} from '../dev/dev';
import { Chart } from 'chart.js';


@IonicPage()
@Component({
  selector: 'page-invested-coins',
  templateUrl: 'invested-coins.html',
})
export class InvestedCoinsPage {

  labelsC;
  dataROI;
  dataInvested;
  dataBal;

  detailToggle = [];
  details: any;
  chart = [];
  coins = [];
  coinsTemp = [];
  totalROIBTC: any;
  totalROIINR: any;
  assetsValueINR: any;
  assetsValueBTC: any;
  assetsInvestmentINR: any;
  assetsInvestmentBTC: any;
  coinSymbols: String;
  constructor(public navCtrl: NavController,public storage: Storage,
    public http: Http, public toastCtrl: ToastController,/* private network: Network,  private toast: ToastController*/
    private alertCtrl: AlertController, public loadingCtrl: LoadingController, public dataP: CoinsServiceProvider) {

  }
  //Get invested coin symbol and join them to string
  getSymboldFromStorage(){
      return this.storage.get("savedCoins").then( (val)=>{
      if(val == null)
        return;
      this.coins = JSON.parse(val);
      console.log("storage");
      console.log(this.coins);
      let cSymbols = '';
      for(let i=0;i<this.coins.length;i++)
        cSymbols +=this.coins[i]["symbol"] + ",";
      this.coinSymbols = cSymbols;
      console.log(this.coinSymbols);
    }, (err)=>{
      console.log(err);
    });
  }

  //On view enter
  ionViewWillEnter() {
    console.log("Ion view enter");
    this.totalROIBTC = 0;
    this.totalROIINR = 0;
    this.assetsValueBTC = 0;
    this.assetsValueINR = 0;
    this.assetsInvestmentBTC = 0;
    this.assetsInvestmentINR = 0;
    let loader = this.loadingCtrl.create({
      content: 'Refreshing...',
      spinner: 'crescent'
    });
    let toast = this.toastCtrl.create({
      message: 'Refreshed successfully',
      duration: 3000,
    });

    loader.present();
    setTimeout(function(){ loader.dismiss() }, 5000);
    console.log("calling getSymboldFromStorage");
    this.getSymboldFromStorage().then(()=>{
      if(!this.coins){
        loader.dismiss();
        toast.present();
        console.log("In if return ");
        return;
      }
      console.log("Didnt return ion view enter");
      this.dataP.getCoinsValue(this.coinSymbols).subscribe( (res) => {
        console.log("component: ",res);
        for(let i=0;i<this.coins.length;i++){
          let symb = this.coins[i]["symbol"];
          this.coins[i]["currCostINR"] = res["RAW"][symb]["INR"]["PRICE"];
          this.coins[i]["currCostBTC"] = res["RAW"][symb]["BTC"]["PRICE"];
          this.assetsValueBTC += parseFloat(this.coins[i]["units"])*(this.coins[i]["currCostBTC"]);
          this.assetsValueINR += parseFloat(this.coins[i]["units"])*(this.coins[i]["currCostINR"]);
          this.assetsInvestmentBTC += parseFloat(this.coins[i]["expenseBTC"]);
          this.assetsInvestmentINR += parseFloat(this.coins[i]["expenseINR"]);
          this.coins[i]["roiBTC"] =  parseFloat(this.coins[i]["units"])*(this.coins[i]["currCostBTC"]) - parseFloat(this.coins[i]["expenseBTC"]);
          console.log("price btx", this.coins[i]["roiBTC"]);
          this.coins[i]["roiINR"] =  parseFloat(this.coins[i]["units"])*(this.coins[i]["currCostINR"]) - parseFloat(this.coins[i]["expenseINR"]);
          this.totalROIBTC += this.coins[i]["roiBTC"];
          this.totalROIINR += this.coins[i]["roiINR"];
          this.coins[i]["roiBTC"] = this.coins[i]["roiBTC"].toPrecision(6);
          console.log("price btx", this.coins[i]["roiBTC"]);
          this.coins[i]["roiINR"] = Math.round(this.coins[i]["roiINR"]);
          this.coins[i]["market"] =  {
            lastUpdate: res["DISPLAY"][symb]["BTC"]["LASTUPDATE"],
            vol24: res["DISPLAY"][symb]["BTC"]["VOLUMEDAYTO"],
            pc24: res["DISPLAY"][symb]["BTC"]["CHANGEPCT24HOUR"],
            priceBTC: res["DISPLAY"][symb]["BTC"]["PRICE"],
            lastMarket: res["DISPLAY"][symb]["BTC"]["LASTMARKET"],
            totalSupply: res["DISPLAY"][symb]["BTC"]["SUPPLY"],
            marketCap: res["DISPLAY"][symb]["BTC"]["MKTCAP"],

          };
        }
        this.coinsTemp = this.coins;
        loader.dismiss();
        toast.present();
        this.totalROIBTC = this.totalROIBTC.toPrecision(6);
        this.totalROIINR = this.totalROIINR.toPrecision(8);
        this.assetsInvestmentINR = this.assetsValueINR.toPrecision(8);
        this.assetsInvestmentBTC = this.assetsValueBTC.toPrecision(6);
      });
    });
  }

  coinPlot(coin,index){
    console.log("index is: ",index);
    console.log("coin is: ",coin);
    console.log("plottigm");
    if(this.detailToggle[index]){
      this.detailToggle[index] = false;
    }
    else{
      let scale = {
            xAxes: [{
                display: false
            }],
            yAxes: [{
              color: '#fff'
            }]
      };
      this.detailToggle.fill(false);
      this.detailToggle[index] = true;
      this.dataP.getChart(coin.symbol).subscribe( res => {
        console.log(res);
        setTimeout(()=>{
          res = JSON.parse(res['_body']);
          let coinHistroy = res['Data'].map((a) => (a.close));
          this.chart[index] = new Chart('canvas'+index,{
            type: 'line',
            data: {
              labels: coinHistroy,
              datasets: [{
                data: coinHistroy,
                borderColor: '#3CBA9F',
                fill: false
              }],
            },
            options: {

              tooltips: {
                callbacks: {
                  label: function(tooltipItems, data){
                    return "â‚¹ "+tooltipItems.xLabel.toString();
                  }
                }
              },
              /*
              tooltips: {
                callbacks: {
                  title: function(tooltipItem, data) {
                    return tooltipItem[0].yLabel;
                  },
                  label: function(tooltipItem, data) {
                    return ' ' + data.datasets[tooltipItem.datasetIndex].label + ' ' + tooltipItem.yLabel;
                  }
                }
              },*/
              responsive: true,
              legend: {
                display: false,
              },
              scales: scale
           }

          })
        }, 250)
      })
    }
  }

  onInput(ev) {
    console.log("called");
    // Reset items back to all of the items
    this.coins = this.coinsTemp
    // set q to the value of the searchbar
    var q = ev.target.value;
    console.log(q);
    // if the value is an empty string don't filter the items
    if (!q) {
      return;
    }
    this.coins = this.coins.filter((v) => {
      if(v.name && q) {
        if (v.name.toLowerCase().indexOf(q.toLowerCase()) > -1) {
          return true;
        }
        return false;
      }
    });
    console.log(q, this.coins.length);
  }

  randColor()
  {
     var r = Math.floor(Math.random() * 255);
     var g = Math.floor(Math.random() * 255);
     var b = Math.floor(Math.random() * 255);
     return "rgb(" + r + "," + g + "," + b + ")";
  }
  drawROIChart(){
    console.log(this.labelsC);
    let colors = [];
    for(let i=0;i<this.labelsC.length;i++){
      colors.push(this.randColor());
    }
    let data = {
      datasets: [{
          data: this.dataROI,
          backgroundColor: colors,
      }],
      labels: this.labelsC,
    };
    this.chart = new Chart('canvas',{
        type: 'bar',
      data: data
    })
  }
  drawBALChart(){
    console.log(this.dataBal);
    let colors = [];
    for(let i=0;i<this.labelsC.length;i++){
      colors.push(this.randColor());
    }
    let data = {
      datasets: [{
          data: this.dataBal,
          backgroundColor: colors,
      }],
      labels: this.labelsC,
    };
    this.chart = new Chart('canvas',{
      type: 'bar',
      data: data,
      responsive: true,
    })
  }
  drawInvestedChart(){
    console.log(this.dataInvested);
    let colors = [];
    for(let i=0;i<this.labelsC.length;i++){
      colors.push(this.randColor());
    }
    let data = {
      datasets: [{
          data: this.dataInvested,
          backgroundColor: colors,
      }],
      labels: this.labelsC,
    };
    this.chart = new Chart('canvas',{
      type: 'bar',
      data: data
    })
  }
  newCoins(){
    this.navCtrl.push(CoinsListingPage);
  }
  edit(id,cname,csymbol){
    this.navCtrl.push(AddCoinPage,{
      id: id,
      name: cname,
      symbol: csymbol,
      add: false
    })
  }
  delete(i){
    let alert = this.alertCtrl.create({
    title: 'Confirm delete',
    message: 'Are you sure, you want to delete?(This operation is irreverseable)',
    buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Delete',
          handler: () => {
            console.log('Buy clicked');
            let temp = [];
            for(var ind=0;ind<this.coins.length;ind++){
              if(ind!=i)
                temp.push(this.coins[ind]);
            }
            console.log(i);
            this.storage.set("savedCoins",JSON.stringify(temp)).then(()=> this.ionViewWillEnter());
          }
        }
      ]
    });
    alert.present();
  }
  refresh(){
    this.ionViewWillEnter();
  }
  showDev(){
    this.navCtrl.push(DevPage);
  }
  /*
  ionViewDidEnter(){

    this.network.onConnect().subscribe(data => {
      console.log(data);
      this.displayNetworkUpadte(data.type);
    }, err => console.log(err));
    this.network.onDisconnect().subscribe(data => {
      console.log(data);
      this.displayNetworkUpadte(data.type);
    }, err => console.log(err));
  }

  displayNetworkUpadte(connState: string){
    let networkType = this.network.type;
    this.toast.create({
      message: 'You are now ' + connState + ' via ' + networkType,
      duration: 3000
    }).present();
  }*/
  }



