import { Component } from '@angular/core';
import { IonicPage, NavController, LoadingController, ToastController } from 'ionic-angular';
import { Http } from '@angular/http';
import { Storage } from '@ionic/storage';
import { CoinsServiceProvider } from '../../providers/coins-service/coins-service';
import { AddCoinPage } from '../add-coin/add-coin';

@IonicPage()
@Component({
  selector: 'page-coins-listing',
  templateUrl: 'coins-listing.html',
})
export class CoinsListingPage {

  coins: any;
  objectKeys = Object.keys;
  coinsTemp: any;
  searchTerm: any;
  investedCoins: any;
  url = "https://api.coinmarketcap.com/v1/ticker/?convert=INR&limit=50&start=";
  range: string;
  displayAddCoins: boolean;

  constructor(public navCtrl: NavController,public http: Http, public loadingCtrl: LoadingController,
    public toastCtrl: ToastController, public datas: CoinsServiceProvider, public storage: Storage) {
    this.displayAddCoins = false;
    this.range = "Top500";
    let loader = this.loadingCtrl.create({
      content: "Loading a lot of data. Please be patient...",
      spinner: 'crescent'
    });
    let toast = toastCtrl.create({
      message: 'Coins were loaded successfully',
      duration: 3000,
    });
    loader.present();
    this.datas.getCoinsList(0).subscribe( res => {
      this.coins = JSON.parse(res["_body"]);
      this.coinsTemp = this.coins;
      console.log(this.coins);
      loader.dismiss();
      toast.present();
      this.storage.get("savedCoins").then( (val)=>{
        this.investedCoins = JSON.parse(val);
        if(this.investedCoins==null){
          this.displayAddCoins = true;
          return;
        }
        console.log("saved coins log",this.investedCoins);
        let cSymbols = [];
        for(let i=0;i<this.investedCoins.length;i++)
          cSymbols.push(this.investedCoins[i]["symbol"]);
        this.investedCoins = cSymbols;
        console.log("invest coin sym array",this.investedCoins);
      });
    })
    /*
    this.getCoins(this.url+"0").subscribe( res => {
      this.coins = res;
      this.coinsTemp = res;
      console.log(this.coins);
      loader.dismiss();
      toast.present();
    });*/
  }

  onInput(ev) {
    console.log("called");
    // Reset items back to all of the items
    this.coins = this.coinsTemp

    // set q to the value of the searchbar
    var q = ev.target.value;
    console.log(q);
    // if the value is an empty string don't filter the items
    if (!q) {
      return;
    }

    this.coins = this.coins.filter((v) => {
      if(v.name && q) {
        if (v.name.toLowerCase().indexOf(q.toLowerCase()) > -1) {
          return true;
        }
        return false;
      }
    });
    console.log(q, this.coins.length);
  }


  onCancel($event){
    this.searchTerm = '';
    this.coins = this.coinsTemp;
  }


  segmentChanged($event){
    let loader = this.loadingCtrl.create({
      content: "Loading a lot of data. Please be patient...",
      spinner: 'crescent'
    });
    let toast = this.toastCtrl.create({
      message: 'Coins were loaded successfully',
      duration: 3000,
    });
    console.log(this.range);
    let tUrl = this.url;
    loader.present();
    if(!this.range.localeCompare("Top500")){
      this.datas.getCoinsList(0).subscribe( res => {
        console.log("Returned data","\n",res);
        this.coins = JSON.parse(res["_body"]);
        this.coinsTemp = this.coins;
        console.log("only data",this.coins);
        loader.dismiss();
        toast.present();
      })
    }
    else{
      this.datas.getCoinsList(500).subscribe( res => {
        console.log("Returned data","\n",res);
        this.coins = JSON.parse(res["_body"]);
        this.coinsTemp = this.coins;
        console.log("only data",this.coins);
        loader.dismiss();
        toast.present();
      })
    }
  }

  getCoins(tUrl){
    console.log(tUrl);
    return this.http.get(tUrl).map(res => (res.json()));
  }

  addCoin(coinId,name,symbol,priceBtc,priceInr){
    this.navCtrl.push(AddCoinPage,{
      id: coinId,
      name: name,
      symbol: symbol,
      add: true,
      priceBTC: priceBtc,
      priceINR: priceInr
    })
  }
}



