import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';
import { DevPage } from '../pages/dev/dev';
import { FaqsPage } from '../pages/faqs/faqs';
import { CoinsListingPage } from '../pages/coins-listing/coins-listing';
import { InvestedCoinsPage } from '../pages/invested-coins/invested-coins';
import { AddCoinPage } from '../pages/add-coin/add-coin';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { CoinsServiceProvider } from '../providers/coins-service/coins-service';

import { HTTP } from '@ionic-native/http';
import { HttpModule } from '@angular/http';
import { IonicStorageModule } from '@ionic/storage';

/*barcode scanner and creator*/
import { NgxQRCodeModule } from 'ngx-qrcode2';

@NgModule({
  declarations: [
    MyApp,
    DevPage,
    FaqsPage,
    CoinsListingPage,
    InvestedCoinsPage,
    AddCoinPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot(),
    NgxQRCodeModule,
    HttpModule,
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    DevPage,
    FaqsPage,
    CoinsListingPage,
    InvestedCoinsPage,
    AddCoinPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    CoinsServiceProvider
  ]
})
export class AppModule {}



